/* eslint-disable */
import { getInfo, setInfo } from "@/utils/storge"
export default {
  namespaced: true,
  state() {
    return {
      userInfo: getInfo()
    }
  },
  mutations: {
    setUserInfo(state, obj) {
      // 把新值进行本地存储
      setInfo(obj)
      state.userInfo = getInfo()
    }
  },
  actions: {
    // 退出登录把用户数据和购物车数据全删除
    logout(context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  }
}
