<template>
  <div class="home">
    <!-- 导航条 -->
    <van-nav-bar title="我的淘宝" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请在此输入搜索关键词"
      @click="$router.push('/search')"
    />

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="it in bannerList" :key="it.imgUrl">
        <img :src="it.imgUrl" alt />
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5" icon-size="40">
      <van-grid-item
        v-for="item in navList"
        :key="item.imgUrl"
        :icon="item.imgUrl"
        :text="item.text"
        @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt />
    </div>

    <!-- 猜你喜欢 -->
    <div class="guess">
      <p class="guess-title">—— 猜你喜欢 ——</p>

      <div class="goods-list">
        <GoodsItem v-for="item in proList" :key="item.goods_id" :it="item"></GoodsItem>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import GoodsItem from "@/components/GoodsItem.vue";
import { getHomeData } from "@/api/home";
export default {
  name: "HomePage",
  components: {
    GoodsItem
  },
  data() {
    return {
      // 轮播图
      bannerList: [],
      // 导航
      navList: [],
      proList: []
    };
  },
  async created() {
    const {
      data: { pageData }
    } = await getHomeData();
    console.log(pageData);
    this.bannerList = pageData.items[1].data;
    this.navList = pageData.items[3].data;
    this.proList = pageData.items[6].data;
  }
};
</script>

<style lang="less" scoped>
.home {
  padding-top: 100px;
  padding-bottom: 50px;
}

.van-nav-bar {
  z-index: 999;
  background-color: black;
  ::v-deep .van-nav-bar__title {
    color: #fff;
  }
}

.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}

.my-swipe .van-swipe-item {
  height: 185px;
  color: #fff;
  font-size: 20px;
  text-align: center;
  background-color: #39a9ed;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 185px;
}

.main img {
  display: block;
  width: 100%;
}

.guess .guess-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}

.goods-list {
  background-color: #f6f6f6;
}
</style>
